import { Point } from 'src/App/types';

/**
 *      (part   0  1)  ,
 *     (startPoint, controlPoint, endPoint).
 *     -   ,
 *   ,    .
 */
export const getQuadraticBezierXYatT = (
  startPoint: Point,
  controlPoint: Point,
  endPoint: Point,
  part: number,
) => {
  const x =
    (1 - part) ** 2 * startPoint.x +
    2 * (1 - part) * part * controlPoint.x +
    part ** 2 * endPoint.x;

  const y =
    (1 - part) ** 2 * startPoint.y +
    2 * (1 - part) * part * controlPoint.y +
    part ** 2 * endPoint.y;

  return { x, y };
};
